.sidebar {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: inherit; }

.sidebar.isHidden {
  width: 0;
  min-width: 0;
  overflow: hidden; }

.grip {
  width: .75rem;
  height: 100%;
  cursor: ew-resize;
  user-select: none;
  background: --primaryForegroundColor;
  opacity: 0; }

/* Large `:after` pseudoclass prevents underlying `selection` to occur. */
.grip:after {
  content: '';
  position: absolute;
  width: 100rem;
  height: 100%;
  margin-left: -50rem;
  display: none; }

.grip:hover,
.grip:active {
  opacity: .05; }

.grip:active:after {
  display: block; }

.grip:before {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  width: .25rem;
  height: .25rem;
  margin-left: -.1rem;
  margin-top: -.1rem;
  border-radius: 50%;
  background: --primaryBackgroundColor;
  z-index: 1;
  box-shadow: 0 0.4rem 0 --primaryBackgroundColor, 0 .8rem 0 --primaryBackgroundColor; }

.detail {
