.filterBar {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-bottom: 1.125rem;
  margin-top: 0.125rem; }

.filterGroupSeparator {
  border-right: 1px solid --ruleColor;
  height: 2rem;
  margin-right: 0.75rem;
  margin-bottom: 0.75rem; }

.filterShowAll, .filterShowAll:hover, .filterShowAll:active {
  background: transparent;
  border: none;
  color: --linkColor;
  margin-bottom: 0.75rem; }

.filterShowAll:hover {
  text-decoration: underline; }

.filterShowAll:active {
  color: --linkActiveColor; }

.filterShowAll:focus {
  outline: none; }



