.activityBar {
  display: flex;
  flex-direction: column;
  width: 3.5rem; }

.activityBarButton {
  position: relative;
  background: transparent;
  border: none;
  color: --activityBarInactiveColor;
  margin: 0.5rem 0;
  padding: 0.25rem 0 0;
  text-align: center;
  cursor: pointer; }

.activityBarButton:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 0.25rem;
  height: 100%;
  background: --accentColor;
  opacity: 0; }

.activityBarButton:hover {
  color: --activityBarHoverColor;
  opacity: .85; }

.activityBarButton:active:after {
  opacity: 1; }

.activityBarButtonActive {
  color: --activityBarActiveColor; }

.activityBarIcon {
  display: inline;
  height: 2rem;
  width: 2rem;
  color: --headerForegroundColor; }



