@import url(normalize.css);
@font-face {
  font-family: 'Selawik';
  src: url("assets/selawk.woff2") format("woff2"), url("assets/selawk.woff") format("woff"); }

@font-face {
  font-family: 'Selawik Light';
  src: url("assets/selawkl.woff2") format("woff2"), url("assets/selawkl.woff") format("woff"); }

html {
  font-size: small;
  line-height: normal; }

body {
  font-family: 'Segoe UI', 'Selawik', Tahoma, Geneva, Verdana, sans-serif;
  height: 100%;
  margin: 0;
  width: 100%;
  overflow: hidden;
  /* prevent touchpads from dragging the window around */ }

pre {
  margin: 0; }

[type="button"] {
  cursor: pointer; }

[type="button"]::-moz-focus-inner {
  border: 0px; }

[type="button"]:focus {
  outline: none; }

button {
  background-color: transparent;
  border: none;
  margin: 0;
  padding: 0;
  cursor: pointer;
  color: --primaryForegroundColor; }

::-webkit-scrollbar {
  width: 0.5rem;
  height: 0.5rem; }

::-webkit-scrollbar * {
  background: transparent; }

::-webkit-scrollbar-thumb {
  background: --scrollbarColor; }

::-webkit-scrollbar-corner {
  background: transparent; }

.shell {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
  width: 100vw; }

.shellContent {
  background-color: --primaryBackgroundColor;
  display: flex;
  flex-direction: row;
  flex: auto;
  color: --primaryForegroundColor;
  overflow: hidden; }

.shellContentActivityBar {
  background-color: --activityBarBackgroundColor;
  flex: none; }

.shellContentDetail {
  display: flex;
  flex: auto;
  flex-direction: column;
  min-width: 0;
  overflow: hidden; }

.shellStatusBar {
  flex: none;
  position: relative;
  z-index: 100; }



