.details {
  display: flex;
  flex-direction: column;
  height: 100%;
  position: relative;
  z-index: 2;
  min-height: 2.5rem;
  max-height: calc( 100vh - 24.5rem);
  padding-top: .9rem;
  box-sizing: content-box; }

@media all and (max-height: 350px) {
  .details {
    min-height: 0; }
  .grip {
    display: none; } }

@media all and (max-height: 313px) {
  .details {
    padding-top: 0; } }

.detailTabTitles {
  padding-top: 0.5rem; }

.detailTabContent {
  overflow-x: hidden;
  overflow-y: hidden; }

.detailTabTitles,
.details .detailTabContent {
  padding-left: 1.5rem;
  padding-right: 1.5rem; }

.detailTabContent:hover {
  overflow-x: auto;
  overflow-x: overlay;
  overflow-y: auto;
  overflow-y: overlay; }

.hide {
  position: absolute;
  right: 0;
  top: -2rem;
  width: 2rem;
  height: 2rem;
  background: cyan; }

.grip {
  position: absolute;
  width: 100%;
  height: .75rem;
  top: .15rem;
  cursor: ns-resize;
  user-select: none;
  background: --sideBarBackgroundColor;
  opacity: 0; }

/* Large `:after` pseudoclass prevents underlying `selection` to occur. */
.grip:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100rem;
  margin-top: -50rem;
  display: none;
  z-index: 2; }

.grip:hover,
.grip:active {
  opacity: 1; }

.grip:active:after {
  display: block; }

.grip:before {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  width: .25rem;
  height: .25rem;
  margin-left: -.1rem;
  margin-top: -.1rem;
  border-radius: 50%;
  background: --primaryBackgroundColor;
  z-index: 1;
  box-shadow: 0.4rem 0 0 --primaryBackgroundColor, .8rem 0 0 --primaryBackgroundColor; }



