.history {
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 0;
  right: 0;
  top: 0; }

.historyContainer {
  position: relative;
  overflow-y: auto;
  flex-grow: 1; }

.text {
  padding: 1rem;
  color: --softerTextColor; }

.text::selection {
  background: --primaryForegroundColor;
  color: --primaryBackgroundColor; }



