.sideBar {
  background-color: --sideBarBackgroundColor;
  display: flex;
  flex-direction: column;
  flex-grow: 1; }

.title {
  padding: 0.75rem 1rem 0.75rem 1rem; }

.titleRequests {
  font-weight: bold;
  float: left;
  color: --headerForegroundColor; }

.rightButton {
  color: --linkColor;
  float: right;
  display: none; }

.rightButton:hover {
  text-decoration: underline;
  cursor: pointer; }



