.container {
  box-sizing: border-box;
  position: relative;
  height: 15rem;
  background-color: --primaryBackgroundColor;
  padding: 2rem 1rem 1rem;
  border-top: 0.5rem solid transparent;
  transition: transform 0.6s ease, border-color 0.3s ease, margin-top 0.6s ease;
  z-index: 1; }

.container.statusDisconnected.containerCollapsed {
  transition: transform 0.6s ease, border-color 0s, margin-top 0.6s ease;
  transform: translateY(calc(100% - 0.5rem + 1px)); }

.container.statusConnected.containerCollapsed {
  transform: translateY(100%); }

.container.containerCollapsed {
  margin-top: -15rem;
  box-shadow: none; }

.container.statusDisconnected {
  border-top-color: --errorColor; }

.container.statusConnected {
  border-top-color: #008A00; }

.heading {
  font-size: 2rem;
  font-weight: bold; }

.subheading {
  font-size: 1rem;
  line-height: 1.5; }

.paragraph {
  color: --softTextColor;
  line-height: 1.5; }

.button {
  padding: 0.5rem 1rem;
  background-color: --accentColor;
  position: absolute;
  bottom: 1rem;
  right: 1rem;
  transition: background-color 0.3s ease; }

.container.statusConnected > .button {
  background-color: #008A00; }

.octopus {
  height: 8rem;
  width: 8rem;
  position: absolute;
  top: -4.25rem;
  right: 1rem;
  background-repeat: no-repeat;
  transition: transform 0.3s ease; }

.container.containerCollapsed > .octopusConnected {
  transform: translateY(50%); }



