.statusLabelContainer {
  display: flex;
  align-items: center; }

.statusLabelUnavailableDescription {
  color: --softTextColor; }

.statusLabelBlankIcon,
.statusLabelBlueIcon,
.statusLabelGreenIcon,
.statusLabelYellowIcon,
.statusLabelRedIcon {
  width: 1rem;
  height: 1rem;
  flex-shrink: 0;
  margin-right: 2px; }

.statusLabelBlueIcon {
  color: --statusCodeInformationalColor; }

.statusLabelGreenIcon {
  color: --statusCodeSuccessColor; }

.statusLabelYellowIcon {
  color: --statusCodeRedirectionColor; }

.statusLabelRedIcon {
  color: --statusCodeErrorColor; }



