svg.octopus {
  overflow: visible; }

.octopusFront,
.octopusBack {
  animation: octopusFloat 1.5s ease-in-out forwards alternate infinite; }

.octopusBack {
  animation-delay: -100ms; }

.connected > .octopusFront,
.connected > .octopusBack {
  animation: octopusPlug 0.3s 2s ease-in-out forwards; }

.eye {
  animation: blink 5s step-start infinite; }

@keyframes octopusPlug {
  to {
    transform: translateX(-13px); } }

@keyframes octopusFloat {
  from {
    transform: translateY(-5%); }
  to {
    transform: translateY(5%); } }

@keyframes blink {
  from, to, 8%, 78%, 88% {
    opacity: 1; }
  10%, 80%, 90% {
    opacity: 0; } }



