.overview {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  border-right: 1px solid transparent;
  border-color: --ruleColor;
  min-width: 7rem; }

.overviewTitle {
  flex: none;
  padding-bottom: 0.5rem;
  font-size: 1.25rem;
  color: --headerForegroundColor; }

.overviewDetailContainer {
  overflow-y: auto;
  position: relative;
  flex-grow: 1; }

.overviewDetail {
  flex: auto;
  position: absolute;
  left: 0;
  right: 10px;
  top: 0; }

.overviewSubDetail {
  padding-left: 2rem; }

.overviewDetailItem {
  margin-bottom: 1rem; }

.overviewDetailItem div {
  white-space: nowrap; }

.overviewDetailTitle {
  margin: 0.5rem 0 0.25rem;
  color: --secondaryForegroundColor;
  display: flex;
  flex-direction: row;
  align-items: center; }
  .overviewDetailTitle > * {
    flex-shrink: 0;
    flex-basis: auto; }
  .overviewDetailTitle > .overviewDetailTitleText {
    flex-shrink: 1;
    margin-right: .25rem; }

.overviewDetailTitleText {
  text-overflow: ellipsis;
  overflow: hidden; }

.overviewDetailIcon,
.overviewInfoIcon,
.overviewChevronUpIcon {
  height: 1.25rem;
  width: 1.25rem;
  cursor: pointer;
  padding-left: 0.25rem;
  padding-right: 0.25rem; }
  .overviewDetailIcon:hover,
  .overviewInfoIcon:hover,
  .overviewChevronUpIcon:hover {
    background-color: --sideBarActionHoverBackgroundColor; }

.overviewChevronUpIcon {
  height: 1rem;
  width: 1rem;
  margin-left: auto;
  position: relative; }
  .overviewChevronUpIcon:after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1; }

.overviewDetailIcon {
  margin-left: .25rem;
  margin-right: 1.75rem; }

.overviewInfoIcon {
  margin-right: .25rem;
  padding: 0.2rem 0.25rem;
  height: 1.7rem;
  width: 1.75rem;
  box-sizing: border-box; }

.overviewChevronIconPath {
  fill: --primaryForegroundColor; }

.overviewDetailIconPath,
.overviewInfoIconPath {
  fill: --linkColor;
  color: --secondaryForegroundColor; }



