.detail {
  border-top-width: 0.25rem;
  border-top-style: solid;
  border-top-color: --accentColor;
  display: flex;
  flex-direction: row;
  height: 100%; }

.detailPanel {
  display: flex;
  flex: none;
  flex-direction: column;
  width: 50%; }

.detailMargin {
  background-color: --ruleColor;
  flex: none;
  width: 1px; }

.detailPanelTitle {
  align-items: center;
  background-color: --detailBiPanelHeaderColor;
  color: --primaryForegroundColor;
  display: flex;
  flex: none;
  font-weight: bold;
  padding: 0.5rem 2rem 0.5rem 1.5rem; }

.detail.isClickableTitles .detailPanelTitle:hover {
  cursor: pointer;
  opacity: .85; }

.detailPanelContent {
  display: flex;
  flex: auto;
  overflow: hidden; }



