.requestButton {
  background: transparent;
  border: none;
  color: inherit;
  flex: none;
  margin: 0;
  padding: 0.75rem 1rem 0.75rem 1rem; }

.requestButton:hover {
  background-color: --hoverRecordColor; }

.requestButton.requestButtonActive {
  background-color: --accentColor;
  position: -webkit-sticky;
  position: sticky;
  bottom: -1px; }

.requestContainer {
  display: flex;
  flex-direction: column; }

.requestSummaryLine, .requestDetailLine {
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.requestSummaryLine {
  color: --primaryForegroundColor;
  font-size: 1rem; }

.requestDetailLine {
  color: --secondaryForegroundColor;
  font-size: 0.75rem;
  margin-top: 0.3rem; }

.requestStatusContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  max-width: 70%; }

.requestStatusContainer > span {
  margin-right: 2px; }

svg.requestClientIcon {
  height: 0.85rem;
  margin-right: 0.25rem;
  color: --secondaryForegroundColor; }

.requestButton.requestButtonActive .requestSummaryLine,
.requestButton.requestButtonActive .requestDetailLine,
.requestButton.requestButtonActive svg.requestClientIcon {
  color: --activeRequestTextColor; }

.statusLabel {
  max-width: 85%; }

.duration {
  margin-top: .045rem; }



