.timeline {
  display: flex;
  flex-direction: column;
  width: 100%; }

.timelineScrollingPanel {
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  overflow-y: hidden; }

.timelineOverview {
  flex: none;
  margin: 1rem 16px 0 6px; }

.timelineDetails {
  margin: calc(0.25rem + 3px) 0px 0 6px; }

@media (max-height: 700px), (max-width: 1000px) {
  .timelineScrollingPanel {
    overflow-y: auto;
    overflow-y: overlay; }
  .timelineDetails {
    flex: none; } }



