.jsonTableContainer {
  overflow-x: auto;
  position: relative; }

.jsonTable {
  table-layout: auto; }

.jsonTableFaderEven, .jsonTableFaderOdd {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 1rem; }

.jsonTableFaderEven {
  background-image: linear-gradient(to bottom, transparent, --tableRowEvenBackgroundColor 40%); }

.jsonTableFaderOdd {
  background-image: linear-gradient(to bottom, transparent, --tableRowOddBackgroundColor 40%); }

.jsonTable tr:nth-child(even) {
  background-color: transparent !important; }

.jsonTable > thead > tr > th {
  white-space: nowrap; }

.jsonTable > thead {
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: --tableHeaderBorderColor;
  padding-bottom: 0.25rem; }

.jsonTableHeaderLabel {
  padding: 0.3rem 0.75rem !important; }

.jsonTableHeaderCount {
  font-weight: normal;
  color: --secondaryForegroundColor; }

.jsonTableHeadSpacer {
  height: 0.125rem !important; }

.jsonTableCell {
  position: relative; }

.jsonTableObjectCell > ul {
  display: inline-block;
  vertical-align: top;
  margin: 0 !important;
  background-color: transparent !important; }

.jsonTableObjectCell > ul > li:first-child {
  padding-top: 0 !important; }

.jsonTableObjectCell > ul > li > div:first-child {
  padding-right: 0.15em !important; }

.jsonTableObjectCell > ul > li > label:first-of-type {
  display: none !important; }

.jsonTableObjectCell > ul > li > span:first-of-type {
  padding-left: 0 !important; }



