.filterButton, .filterButtonShown, .filterButtonNotShown {
  background-color: transparent;
  margin-right: 0.75rem;
  margin-bottom: 0.75rem;
  color: --primaryForegroundColor;
  padding: 0.313rem;
  padding-left: 0.438rem;
  padding-right: 0.438rem; }

.filterButtonShown {
  border: 1px solid transparent;
  border-color: --filterButtonBorderColor; }

.filterButtonShown:hover {
  border: 1px solid transparent;
  border-color: --filterButtonHoverBorderColor;
  background-color: --filterButtonHoverBackgroundColor; }

.filterButtonNotShown {
  border: 1px solid transparent; }

.filterButtonNotShown:hover {
  background-color: --filterButtonHoverBackgroundColor;
  border: 1px solid transparent; }

.filterButtonNotShown:active {
  background-color: --filterButtonActiveBackgroundColor; }

.filterButtonContent {
  align-items: center;
  display: flex;
  flex-direction: row;
  min-height: 1.25rem; }

.filterButtonIcon {
  margin-right: 0.219rem;
  width: 1rem; }



