.errorScreen {
  background: --primaryBackgroundColor;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 101;
  letter-spacing: .025rem;
  animation: fadeIn .35s ease-out; }

.imageWithText {
  width: 30rem;
  margin: 0 auto;
  text-align: center;
  font-size: 12px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: --softTextColor; }

.image {
  width: 18rem;
  height: 12rem;
  margin: 0 auto;
  background-image: url("assets/images/error/error-octopus.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% auto;
  animation: zoomIn 0.7s cubic-bezier(0, 0.305, 0, 1.005); }

.description {
  margin-top: 1.5rem; }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes zoomIn {
  from {
    transform: scale(0.95); }
  to {
    transform: scale(1); } }



