.sideBar {
  display: flex;
  flex: none;
  flex-direction: column; }

.sideBarTitleHolder {
  background-color: --sideBarHeaderColor;
  border-top-width: 1px;
  border-top-style: solid;
  border-top-color: --sideBarBorderColor;
  flex: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0.25rem .5rem .25rem 1rem; }

.sideBarTitle {
  color: --headerForegroundColor; }

