.strip {
  display: flex;
  flex-direction: column;
  flex: auto;
  overflow: hidden;
  width: 100%; }

.stripButtonHolder {
  display: flex;
  flex: none;
  flex-direction: row; }

.stripButtonHolder.stripPlain {
  padding-left: 2rem;
  padding-right: 2rem; }

.stripButton {
  background: transparent;
  border: none;
  color: inherit;
  margin: 0;
  padding: 0.25rem 0; }

.stripButton:hover {
  color: --linkColor; }

.stripButtonActive {
  color: --tabStripButtonActiveColor; }

.stripPlain .stripButton {
  margin: 0 1.5rem; }

.stripPlain .stripButtonActive {
  border-bottom-width: 0.2rem;
  border-bottom-style: solid;
  border-bottom-color: --tabStripButtonActiveColor; }

.stripTabs {
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: --tabStripButtonBorderColor; }

.stripTabs .stripButton {
  border: 1px solid transparent;
  margin: 0 0.25rem;
  padding: 0.25rem 0.5rem 0.5rem 0.5rem;
  margin-bottom: -1px; }

.stripTabs .stripButtonActive {
  background-color: --primaryBackgroundColor;
  border-width: 1px;
  border-style: solid;
  border-top-color: --tabStripButtonBorderColor;
  border-right-color: --tabStripButtonBorderColor;
  border-bottom-color: transparent;
  border-left-color: --tabStripButtonBorderColor; }

.stripPlain .stripButton:first-child,
.stripTabs .stripButton:first-child {
  margin-left: 0; }

.stripPlain .stripButton:last-child,
.stripTabs .stripButton:last-child {
  margin-right: 0; }

.stripDetail {
  display: flex;
  flex: auto;
  overflow: hidden;
  padding-left: 2rem;
  padding-right: 2rem; }

.stripContainerModal .stripDetail > div {
  overflow-y: auto;
  height: calc(100vh - 17rem);
  width: 100%; }

.stripContainerSection .stripDetail > div {
  overflow-y: hidden;
  height: 35vh;
  width: 100%;
  margin-bottom: 0.5rem; }



