.middleware {
  flex: none; }

.middlewareTitle {
  align-items: baseline;
  display: flex;
  flex-direction: row;
  color: --headerForegroundColor; }

.middlewareLegend {
  display: flex;
  flex-direction: row;
  margin-left: 1.5rem; }

.middlewareLegendItem {
  display: flex;
  flex-direction: row;
  margin-right: 1rem; }

.middlewareModifiedHeader {
  /* Note: We add margin to align header with request/response icon in body. */
  margin-left: 1.2rem; }

.middlewareValueHeader {
  overflow: hidden; }

.middlewareBody {
  vertical-align: top;
  color: --tertiaryForegroundColor; }

.middlewareIndent {
  margin-left: 1.5rem; }

.middlewareList {
  align-items: baseline;
  display: flex;
  flex-direction: column; }

.middlewareModifiedItem, .middlewareOperation {
  display: flex;
  flex-direction: row;
  width: 100%; }

.middlewareOperation + .middlewareOperation,
.middlewareModifiedItem + .middlewareModifiedItem {
  margin-top: 0.75rem; }

.middlewareIcon {
  flex: none;
  height: 1.2rem;
  margin-right: 0.2rem;
  width: 1.2rem; }

.middlewareStatusCode {
  /* Account for icon margins in aligning StatusLabel with plain text. */
  margin-left: -0.25rem; }

.middlewareRequestIcon {
  composes: middlewareIcon;
  color: --requestColor; }

.middlewareResponseIcon {
  composes: middlewareIcon;
  color: --responseColor; }

.middlewareOperationType {
  color: --secondaryForegroundColor; }

.middlewareDurationColumn {
  text-align: right; }

.middlewareNameAnonymous {
  color: --secondaryForegroundColor; }



