.timelineDetails {
  display: flex;
  flex-direction: column;
  overflow: hidden; }

.timelineDetailsEvent {
  border-right-style: dashed;
  border-right-width: 1px;
  flex-grow: 1; }

.timelineDetailsSlownessIcon {
  flex: none;
  margin-right: 2px; }

.timelineDetailsActivityBar {
  display: flex;
  justify-content: flex-end;
  flex-direction: row;
  flex-grow: 1;
  height: 0.5rem; }

.timelineDetailsActivityMetadataContainer {
  align-items: center;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: row; }

.timelineDetailsActivityMetadata {
  color: --secondaryForegroundColor;
  display: flex;
  flex-direction: row;
  margin: 0 0.75rem 0 0.75rem; }

.timelineDetailsActivityMetadataPercentage {
  margin: 0 0 0 0.5rem;
  transform: scale(0); }

.timelineDetailsTableRow:hover .timelineDetailsActivityMetadataPercentage {
  transform: scale(1); }

/* Fix for `Edge` only. When got from `scale(0)` to `scale(1)`
   on `.timelineDetailsActivityMetadataPercentage`, we need to
   trigger `paint` event on parent (and not trigger `reflow`)
   to make the `.timelineDetailsActivityMetadataPercentage`
   item to show up.
   Relies on `z-index`, alternatively can rely on `background-attachment`
   or `background-position` (if stacking context is an issue).
*/
.timelineDetailsTableRow {
  z-index: 0; }

.timelineDetailsTableRow:hover {
  z-index: 1; }

/* fix ends; */
.timelineDetailsIndexContainer {
  align-items: center;
  display: flex;
  flex-direction: row;
  overflow-x: hidden; }

.timelineDetailsIndexText {
  margin-left: 0.15rem; }



