.statusBar {
  background-color: --statusBarBackgroundColor;
  color: --statusBarForegroundColor;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  min-height: 1.5rem;
  padding: 0 1rem; }

.statusBarGroup {
  padding: 0 0.5rem;
  display: flex;
  align-items: center; }

.statusBarButton {
  padding: 0 0.30769rem;
  padding-top: .25rem;
  text-align: center; }

.statusBarButton:hover {
  background-color: --statusBarHoverColor; }

.statusBarButtonActive {
  composes: statusBarButton; }

.statusBarButtonIcon {
  height: 1.25rem;
  width: 1.25rem;
  color: #888888; }

.statusBarButton:hover .statusBarButtonIcon, .statusBarButtonActive .statusBarButtonIcon {
  color: #ffffff; }

.version {
  padding: 0 0.30769rem;
  padding-top: 0.07692rem; }



